#menu-icon {
  display: none;
  width: 20px;
  border-width: 0px;
  }
  #menu-icon img {
  width: 100%;
  display: block;
 
  }
  #mobile-menu {
  display: none;


  }
  #mobile-menu {
    position: fixed;
    top: 0;
    margin-left: -400px;
    background: #2a2929;
    padding: 10px;
    z-index: 2;
    bottom: 0;
    width: 105px;
    padding-top: 40px;
    transition: all 0.4s ease-in;
    border: none;
  }
  #mobile-menu ul {
    margin-bottom: 80px;
    
  }
    
  
  #mobile-menu h4 {
    color: #5CA26E;
    font-size: 25px;
    margin-bottom: 25px;
  }
  #mobile-menu  img {
    filter: invert(100%) sepia(0%) saturate(7477%) hue-rotate(15deg) brightness(121%) contrast(100%);
    
  }
  #mobile-menu p{
    color: #fff;
    margin-bottom: 10px;
    font-size: 25px;
  }
  span.overlay {
    background: rgba(0, 0, 0, 0.6);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    z-index: 1;
    display: none;
    }
    span.close {
    position: fixed;
    right: 285px;
    top: 40px;
    width: 32px;
    height: 32px;
    display: block;
    cursor: pointer;
    z-index: 2;
    display: none;
    }
    span.close img {
    display: block;
    width: 100%;
    }
    span.close.active {
    display: block;
    }
    #mobile-menu.active {
    right: 0;
    }
    span.overlay.active {
    display: block;
    }
  #mobile-menu ul li {
  margin-bottom: 20px;
  color: #fff;
  }
  
  #mobile-menu ul li a {
  font-size: 16px;
  font-weight: bold;
  color: #ffffff;
  display: block;
  }
  #mobile-menu ul li a.button {
  padding: 10px 35px;
  border: 1px solid #fff;
  border-radius: 4px;
  
}
  
  
  @media all and (max-width: 480px) {
  #menu-icon {
  display: block;
  }
  #mobile-menu{
  display: block;
  }
  #mobile-menu ul li a {
  display: none;
  }
  #mobile-menu ul li a.button {
  display: block;
  }
  @media all and (max-width: 480px){
  .line{
    width: 22%;
  }
  }
  @media all and (max-width: 768px){
    .line2{
      clear: both;
      border: 1px solid transparent;
      height: 0px;
      
    }
  }
  @media all and (max-width: 768px){
    .line3{
      clear: both;
      border: 1px solid transparent;
      height: 0px;
    }
  }
  @media all and (max-width: 768px){
    .line4{
      clear: both;
      border: 1px solid transparent;
      height: 0px;
    }
  }
  @media all and (max-width: 640px){
